import React from 'react'
import "./Monitize.css";
import { withRouter } from "react-router-dom"
function GetYourLink(props) {
    return (
        <>
            <div className="get_link_back_color">
                <div className="page_heading text-center pt-5">Get Your Own <span className="content_color">ULNK Hub</span></div>
                <div className="mt-2 pb-5 text-center">
                    <button className="Home_page_button" onClick={() => props.history.push("/login")}>Try for Free <i className="fa fa-arrow-right" /></button>
                </div>
            </div>
        </>
    )
}

export default withRouter(GetYourLink)
